// приховує тільки візуально
// аналог appearance: none;
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

// повністю ховає від читалок та фокуса
.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

// для фіксування сторінки при відкритому меню
.overflow-hidden {
  overflow: hidden;
}
